// Linting to prevent bad markup going live 
/* Not added to VMstrap tet */ 
html:not([lang]):after,  
html[lang=""]:after {
	content: "No language tag";
	position: absolute;
	top: 0;
	left: 0;
	background: red!important;
} 

//Incorrect Character Set
meta[charset]:not([charset="UTF-8"]):after {
	content: "Incorrect character set defined";
	position: absolute;
	top: 0;
	left: 0;
	background: red!important;
} 

//Unaccessible Viewport Attributes
meta[name="viewport"][content*="user-scalable=no"]:after,  
meta[name="viewport"][content*="maximum-scale"]:after,  
meta[name="viewport"][content*="minimum-scale"]:after {
	content: "Unaccessible viewport attributes";
	position: absolute;
	top: 0;
	left: 0;
	background: red!important;
}  

// Faulty or Missing Link Targets
a:not([href]),
a[href="#"],  
a[href=""],  
a[href*="javascript:void(0)"],
button:empty,  
a:empty {
	content: "lint";
	outline: 2px solid red!important;
}  


// Unaccessible Images
img:not([alt]) { 
	outline: 2px solid red!important;
}  

//Unlabelled Form Elements
input:not([id]),  
select:not([id]),  
textarea:not([id]) { 
	border: 2px solid red!important;
}

label:not([for]) {
	border: 2px solid red!important;
}  